.feed-index {
  width: 400px;
  margin: 10px;
  padding: 10px;
  color: #f2f2f2;
}

  .blog-index {
    background-color: #27272c;
    height: 400px;
    margin-top: 20px;
    padding: 10px;
    border-style: solid;
    border-radius: 1rem;
    text-align: center;
    align-content: center;
  }
  .blog-index-item {
    padding-top: 40px;
  }
  .blog-index-img-border {
    height: 140px;
    background-color: #353547;
    border-color: #f2f2f2;
    border-style: solid;
    border-radius: 1rem;
  }
  .blog-index-img {
    height: 250px;
    width: 250px;
    object-fit: contain;
    border-radius: 10px;
    align-content: center;
  }
  